import Vue from 'vue'
import VueRouter from 'vue-router'
import accueil from '../components/accueil.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'accueil',
    component: accueil
  },
 
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
